@import url(https://fonts.googleapis.com/css?family=Open+Sans);

body{
  background: #f2f2f2;
  font-family: 'Open Sans', sans-serif;
}

.search {
  width: 100%;
  display: flex;
  transition: .3s;
}
.search:hover{
  transform: scale(1.05);
}

.searchTerm {
  width: 100%;
  border: 3px solid red;
  border-right: none;
  padding: 5px;
  height: 20px;
  border-radius: 5px 0 0 5px;
  outline: none;
  color: #9DBFAF;
}

.searchTerm:focus{
  color: red;
}

.searchButton {
  border: 2px solid red ;
  width: 40px;
  height: 36px;
  background: red;
  text-align: center;
  border-radius: 0 5px 5px 0;
  cursor: pointer;
  font-size: 20px;
}

.search__wrap{
  width: 30%;
}